.container{
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.calculator{
  border-radius: 5px;
  background-color: black;
  width: 350px;
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
}
.display{
  font-family: 'Orbitron', sans-serif;
  padding-top: 4px;
  padding-bottom: 4px;
  width: 96%;
  margin-left: 2%;
  margin-bottom: 1px;
  border-radius: 3px;
  background-color: black;
}

#calculation{
  color: coral;
  font-size: 18px;
}
#input{
  color: white;
  font-size: 22px;
}
#calculation, #input{
  text-align: end;
}
.buttons{
  display: grid;
  grid-gap: 1px;
  justify-content: center;
  grid-template-columns: repeat(4, 24%);
  grid-template-rows: repeat(5, 70px);
  margin-bottom: 5px;
}
button{
  background-color: rgb(102, 95, 95);
  color:white;
  border: none;
  border-radius: 3px;
  font-size: 16px;
  font-family: 'Russo One', sans-serif;
}
button:hover{
  color: black;
  transform: scale(1.02);
  -webkit-box-shadow: 0px 1px 5px -1px rgba(255,255,255,1);
-moz-box-shadow: 0px 1px 5px -1px rgba(255,255,255,1);
box-shadow: 0px 1px 5px -1px rgba(255,255,255,1);
}
button:active{
  outline: none;
  background-color: gray;
}
button:focus{
  outline: none;
}

#clear{
  background-color: tomato;
  grid-column: span 2;
}
#clear:active{
  background-color: indianred;
}
#zero{
  grid-column: span 2;
}
#equals{
  grid-row: span 2;
  background-color: teal;
}
#equals:active{
  background-color: lightseagreen;
}
p{
  margin: 0 0;
}
#regards{
  font-size: 20px;
  font-family: 'Share Tech', sans-serif;
}